<template>
  <div id="container">
    <div id="login-msg">
      <p>登录</p>
    </div>
    <div id="msg">
      <p>{{msg}}</p>
    </div>
    <div id="name">
      <el-input  v-model="name" placeholder="请输入用户名"></el-input>
    </div>
    <div id="password">
      <el-input  placeholder="请输入密码" v-model="password" show-password></el-input>
    </div>
    <div id="buttons">
      <el-button id="login" @click="login">登录</el-button>
      <el-button id="register" @click="goToRegister">注册</el-button>
    </div>
  </div>
</template>

<script>
import { ref } from 'vue'
import Qs from 'qs'

export default{
  setup() {
    return {
      password: ref(''),
      name:ref('')
    }
  },
  name: 'App',
  data(){
    return {
      msg:"",
    }
  },
  components: {
  },
  methods:{
    goToRegister:function(){
      window.location.href='/register';
      return;
    },
    login:async function(){
      if(this.name=="" || this.password == "")
      {
        this.msg = "用户名或密码为空";
        return;
      }
      this.$axios.post('/login',Qs.stringify({
          name:this.name,
          password:this.password
      })).then((response) =>{
        if(response.status == 200)
        {
          if(response.data == "success")
          {
            window.location.href="/hall";
            return;
          }
          this.msg = response.data;
        }
      });
    }
  }
}

</script>

